
.floating-button {
	transition: opacity 0.15s ease-out, transform 0.2s ease-out;
	position: fixed;
	transform: translateX(-50%) translateY(0px);
	bottom: 10px;
	left: 50%;
	width: max-content;

	&.show {
		transform: translateX(-50%) translateY(0);
		z-index: 40;
		opacity: 1;
	}

	&.hide {
		opacity: 0;
		transform: translateX(-50%) translateY(20px);
	}
}

@keyframes slide-up {
	from {
		opacity: 0;
		transform: translateX(-50%) translateY(20px);
	}
	to {
		opacity: 1;
		transform: translateX(-50%) translateY(0);
	}
}

@keyframes slide-down {
	from {
		opacity: 1;
		transform: translateX(-50%) translateY(0);
	}
	to {
		opacity: 0;
		transform: translateX(-50%) translateY(20px);
	}
}


/**
* Reinstate scrolling for non-JS clients
*/

.simplebar-content-wrapper {
	scrollbar-width: auto;
	-ms-overflow-style: auto;
}

.simplebar-content-wrapper::-webkit-scrollbar,
.simplebar-hide-scrollbar::-webkit-scrollbar {
	display: initial;
	width: initial;
	height: initial;
}


.select2-results__options {
	scrollbar-width: thin; /* Firefox */
	scrollbar-color: transparent transparent; /* Firefox */
}

.select2-results__options::-webkit-scrollbar {
	width: 0px; /* Chrome/Safari */
	height: 0px;
}


.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
	background-color: var(--scrollbar-color);
}


.page-full-map {
	.product-sidebar__services {
		margin-top: 20px;
	}
}

.mobile-block {
	@media (min-width: 1200px) {
		display: none;
	}
}


[data-tabs="tabs-panels"] >  *,
.tabs-block  .tab-panel {
	display: none;
}

[data-tabs="tabs-panels"] >  *.active,
.tabs-block  .tab-panel.active {
	display: block;
}

[data-tabs="tabs-panels"] {
	.dimmed {
		opacity: 0.5;
	}
}

[data-tabs="tabs-list"] {
	padding: 0;
	margin: 0;
	list-style: none;
	display: flex;
	border-bottom: 1px solid  #d0d0d0;
	margin-bottom: 16px;
	max-width: 390px;

	@media (min-width: 992px) {
		margin-bottom: 30px;
	}

	& > * {
		flex-basis: 50%;
	}

	li {
		font-weight: 600;
		font-size: 20px;
		color: #d0d0d0;
		padding-bottom: 8px;

		@media (min-width: 360px) {
			padding-left: 20px;
		}

		&.active {
			color: #0069b5;
			border-bottom: 2px solid  #0069b5;
		}
	}
}

//----------------------//

[data-block="less-more"] {
	position: relative;
	overflow: hidden;
}
[data-block="more-elem"] {
	display: block;
	color: var(--primary-color);
	cursor: pointer;
	margin-top: 20px;

	&.collapsed {
		display: none;
	}
}

[data-block="content"] {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	overflow: hidden;

	& > *:last-child {
		margin-bottom: 1px;
	}
}

.modal__text {
	.text-item__definition {
		font-weight: 600;
		font-size: 20px;

		@media (min-width: 992px) {
			font-size: 24px;
		}
	}

	ul {
		li {
			font-weight: 400;
			font-size: 16px;

			@media (min-width: 992px) {
				font-size: 20px;
			}
		}
	}
}