.modal {
	.btn-close {
		position: absolute;
		top: -30px;
		right: 19px;
		font-size: 20px;
		filter: brightness(100);
		background-image: none;
		outline: none;
		box-shadow: none;

		@media (min-width: 992px) {
			right: 0;
		}

		.icon-cross {
			color: #fff;
		}
	}

	.modal-dialog {
		//margin-top: 70px !important;
		//margin-bottom: 20px !important;
	}

	&.full-width {
		.modal-dialog {

			height: calc(100dvh - 100px);

			@media (max-width: 575px) {
				margin: 0;
			}
		}

		.modal-content {
			margin-top: 20px;
			height: calc(100dvh - 75px);
		}
	}

	.modal-body {
		background-color: #fff;
	}

	.modal-body__wrapper {
		overflow: hidden;
		border-radius: 20px;
		overflow-y: auto;
	}
}

#vacancyModal {
	.modal-dialog {
		@media (min-width: 992px) {
			max-width: 898px;
		}
	}

	.modal-header {
		padding: 0;
		border: none;
	}

	.modal-content {
		position: relative;
		overflow: visible;
		padding: 0;
		border-radius: 20px;
		border: none;
	}

	.modal-body {
		padding: 16px;
		border-radius: 20px;

		@media (min-width: 768px) {
			padding: 40px;
		}
	}
}

.modal__text {
	margin-top: 40px;
}

.modal__text-item {
	margin-bottom: 20px;
}

.modal__text-title {
	margin-bottom: 30px;
	font-weight: 600;
	font-size: 24px;

	@media (min-width: 992px) {
		font-size: 32px;
	}
}

.modal__response {
	border-radius: 10px;
	padding: 30px 16px;
	background: linear-gradient(270deg, #1db6e7 0%, #ccfcfc 100%);

	@media (min-width: 992px) {
		padding: 30px 38px;
		margin-top: 40px;
	}

	.form-control {
		border: 1px solid #d0d0d0;
		border-radius: 10px;

		@media (min-width: 768px) {
			font-size: 14px;
		}

		@media (min-width: 992px) {
			font-size: 16px;
		}
	}
}

.modal__response-title {
	font-weight: 600;
	font-size: 24px;
	color: var(--bs-title-color);
	margin-bottom: 30px;

	@media (min-width: 992px) {
		font-size: 38px;
	}
}

.form-check-label {
	font-weight: 400;
	font-size: 16px;
	line-height: 140%;
	color: #222;
}


.drop-zone {
	border: 1px dashed #222;
	border-radius: 10px;
	padding: 22px 20px;
	text-align: center;
	cursor: pointer;
	transition: 0.3s;
	position: relative;
	margin: auto;
	background: #fff;

	&:hover {
		background: #f8f9fa;
	}

	&.dragover {
		background: #e9ecef;
		border-color: #007bff;
	}

	.file-list {
		margin-top: 20px;
		list-style: none;
		padding: 0;
	}

	.file-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		background: #eeeeee;
		padding: 5px 10px;
		border-radius: 5px;
		margin-bottom: 5px;
		font-size: 14px;

		&:last-child {
			margin: 0;
		}
	}

	.remove-file {
		cursor: pointer;
		color: red;
		font-weight: bold;
		font-size: 18px;
	}

	.remove-file:hover {
		color: darkred;
	}

	.drop-zone__element {
		display: flex;
		flex-wrap: nowrap;
		gap: 10px;
		align-content: center;

		@media (min-width: 992px) {
			justify-content: center;
		}

		svg {
			width: 26px;
			height: 26px;
		}

		& > * {
			align-self: center;
			font-weight: 400;
			font-size: 16px;
			line-height: 1.2;
		}

		a {
			text-decoration: none;
			color: var(--primary-color);
		}
	}
}


