.comment-item {

}

.comment-item__header {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	margin-bottom: 10px;
}

.comment-item__user {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
}

.comment-item__date {
	font-size: 14px;
	color: #6f6f6f;
	margin-left: 15px;

	@media (min-width: 992px) {
		font-size: 20px;
		line-height: 140%;
		margin-left: 20px;
	}

	& > * {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.comment-item__user-pic {
	border-radius: 50%;
	overflow: hidden;
	min-width: 68px;
	min-height: 68px;
	max-width: 68px;
	max-height: 68px;

	@media (min-width: 992px) {
		min-width: 90px;
		min-height: 90px;
		max-width: 90px;
		max-height: 90px;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.comment-item__user-info {
	margin-left: 15px;
}

.comment-item__profile-title {
	font-weight: 600;
	font-size: 14px;
	margin-bottom: 7.6px;
	line-height: 1.2;

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.comment-item__profile-rating {
	display: flex;
	flex-wrap: nowrap;
}

.comment-item__profile-rating-value {

}

.comment-item__profile-rating-icon {
	display: flex;
	flex-wrap: nowrap;

	img {
		height: 17px;

		@media (min-width: 992px) {
			height: 22.5px;
		}
	}
}

.comment-item__text {
	[data-block=more-elem] {
		margin-top: 10px;
	}
}
