html {
  transition: opacity .2s ease;

  &.loaded {
    transition: opacity .2s ease;
    opacity: 1 !important;
  }
}

html,
body {
  min-width: 320px;
  width: 100%;
  font-family: var(--bs-font-family);
  background-color: var(--bs-body-bg);
  color: var(--bs-font-color);
  font-size: var(--bs-font-size);
  font-weight: var(--bs-font-weight);
  line-height: var(--bs-line-height);
}

.bg-gray {
  background-color: var(--bs-bg-gray);
}

.section-padding {
  padding: 15px 0;

  @media (min-width: 768px) {
    padding: 30px 0;
  }

  @media (min-width: 1200px) {
    padding: 40px 0;
  }

  &--inner {
    padding: 20px 0;

    @media (min-width: 992px) {
      padding: 30px 0;
    }

    .block-backplate--mobile {
      padding: 16px;

      @media (min-width: 768px) {
        padding: 30px;
      }
    }

    &_small {
      padding: 10px 0;

      @media (min-width: 768px) {
        padding: 15px 0;
      }
    }
  }
}

.section-margin {
  margin: 15px 0;

  @media (min-width: 768px) {
    margin: 30px 0;
  }

  @media (min-width: 1200px) {
    margin: 45px 0;
  }
}

.section-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  color: var(--bs-font-color);

  @media (min-width: 768px) {
    font-size: 26px;
    margin-bottom: 24px;
  }

  @media (min-width: 1200px) {
    font-size: 36px;
    margin-bottom: 40px;
  }

  &--main {
    font-size: 20px;
    margin-bottom: 16px;

    @media (min-width: 375px) {
      text-align: center;
    }

    @media (min-width: 768px) {
      text-align: left;
      font-size: 28px;
    }

    @media (min-width: 1200px) {
      font-size: 38px;
      margin-bottom: 20px;
    }
  }

  &--bigger {
    font-size: 24px;

    @media (min-width: 768px) {
      font-size: 28px;
    }

    @media (min-width: 1200px) {
      font-size: 38px;
    }
  }

  &--color {
    color: var(--bs-title-color);
  }

  &--inner {
    font-size: 24px;

    @media (min-width: 768px) {
      font-size: 28px;
    }

    @media (min-width: 992px) {
      font-size: 32px;
    }

    @media (min-width: 1200px) {
      margin-bottom: 20px;
    }
  }
}

.section-title--mb-20 {
  margin-bottom: 20px;
}

.section-title__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
  margin-bottom: 20px;

  @media (min-width: 1200px) {
    margin-bottom: 38px;
  }

  .section-title {
    margin: 0;
  }
}

.section-subtitle {
  line-height: 136%;

  @media (min-width: 992px) {
    font-size: 20px;
  }
}

.custom-checkbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 23px;
  height: 23px;
  //padding-left: 30px; /* Расстояние между кастомным чекбоксом и текстом */

  input {
    display: none;
  }

  &::before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    width: 23px;
    height: 23px;
    border: 2px solid #6489FF;
    background-color: transparent;
    border-radius: 4px;
  }

  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    font-size: 16px;
    visibility: hidden;
    background-image: url('../images/svg/check.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 15px;
    width: 100%;
    height: 100%;
  }
}

.custom-checkbox {
  &.active {
    &::before {

    }

    &::after {
      visibility: visible;
    }
  }
}

.list-style-none {
  list-style: none;
  padding: 0;
}

.btn {
  display: inline-block;
  color: var(--btn-text);
  background-color: var(--btn-bg);
  border-radius: 65px;
  padding: 12.5px 20px;
  outline: none;
  box-shadow: none;
  border: none;
  font-weight: 600;
  align-content: center;

  svg {
    path {
      transition: all .2s ease;
    }
  }

  @media (min-width: 768px) {
    font-size: 16px;
  }

  span {
    display: inline-block;
  }

  &:hover {
    cursor: pointer;
    background-color: var(--btn-bg-hover);
    color: var(--btn-text);
  }

  &:active {
    cursor: pointer;
    background-color: var(--bs-light-blue) !important;
    color: var(--primary-color) !important;

    svg {
      path {
        fill: var(--primary-color);
      }
    }
  }

  &--outlined {
    background-color: transparent;
    color: var(--primary-color);
    border: 2px solid var(--primary-color);

    &:hover {
      color: #fff;
      border-color: var(--primary-color);
      background-color: var(--primary-color);
    }
  }

  &--light {
    background-color: var(--bs-light-blue);
    color: var(--primary-color-light);
    border-color: var(--bs-light-blue);

    &:hover {
      color: #fff;
      border-color: var(--primary-color-light);
      background-color: var(--primary-color-light);
    }
  }

  &--gray {
    border-radius: 45px;
    background: #eee;
    color: var(--primary-color-light);
    font-weight: 600;
    font-size: 14px;
    padding: 10px 16px;

    &:hover {
      background: #f8f8f8;
      color: var(--primary-color-light);
    }

    @media (min-width: 768px) {
      font-size: 16px;
    }

    &_text-dark {
      color: var(--bs-font-color);

      &:hover {
        color: var(--bs-font-color);
      }
    }
  }

  &--white {
    background: #fff;
    color: var(--bs-font-color);

    &:hover {
      background-color: var(--bs-light-blue);
      color: var(--primary-color);
    }
  }

  &--hover-white {
    &:hover {
      background-color: #fff;
    }
  }

  &--hover-light {
    &:hover {
      background-color: var(--bs-light-blue);
      color: var(--primary-color);
    }
  }

  &--third {
    background: var(--bs-light-secondary);
    border-radius: 10px;
  }

  &--fourth {
    background: var(--primary-color);
    border-radius: 10px;
  }

  &--w-icon {
    display: flex;
    align-items: center;

    [class^="icon-"], [class*="icon-"] {
      font-size: 20px;
    }
  }
}

input.form-control {
  border-radius: 65px;
  padding: 9.5px 20px;
  border: none;

  &.bordered {
    border: 1px solid #000;

    &:hover, &:focus {
      box-shadow: 0 0 0 0;
      border-color: var(--primary-color);
    }
  }
}

select.form-select {
  border-radius: 65px;
  padding: 10px 30px 10px 20px;
  border: none;

  &.bordered {
    border: 1px solid #000;

    &:hover, &:focus {
      box-shadow: 0 0 0 0;
      border-color: var(--primary-color);
    }
  }
}

textarea.form-control {
  border-radius: 20px;
  border: none;
}

input.form-control, textarea, label {
  width: 100%;
}

input.form-control,
select.form-select,
textarea, .btn {
  min-height: 40px;

  @media (min-width: 1200px) {
    font-size: 16px;
  }
}

.invalid-feedback,
.valid-feedback {
  line-height: 1;
  margin-top: 5px;
}

.accent {
  color: var(--primary-color);
}

.container {
  max-width: 1240px;
  --bs-gutter-x: 2rem;

  &--wide {
    max-width: 1400px;
  }
}

main {
  display: block;
  //overflow: hidden;

  &.isHeaderFixed {
    & > * {
      &:first-child {
        padding-top: 92px;
      }
    }
  }
}

.swiper {
  width: 100%;
  height: 100%;
  user-select: none;

  .swiper-slide {
    background-color: transparent;
  }
}


.swiper-button-next, .swiper-button-prev {
  //width: 40px;
  //height: 40px;
  //background-color: rgba(219, 52, 31, 0.3);
  top: auto;
  bottom: var(--swiper-navigation-top-offset, 0%);;
  transition: all .1s ease-out;

  @media (min-width: 768px) {
    width: 50px;
    height: 50px;
    bottom: auto;
    top: var(--swiper-navigation-top-offset, 48.5%);
  }

  &::after {
    color: #000;
    font-size: 30px;
    transition: all .1s ease-in;
  }

  &:hover {
    transition: all .1s ease-in;

    &::after {
      color: var(--primary-color);
    }
  }

  &:active {
    color: var(--primary-color);
    //background-color: rgba(84, 235, 255, 0.3);
    transform: scale3d(0.9,0.9,1);
    //border-radius: 4px;
    transition: all .1s ease-in;
  }
}

.swiper-button-next {
  &:hover {
    &::after {
      transform: translateX(3px);
    }
  }

  right: var(--swiper-navigation-sides-offset, 35%);

  @media (min-width: 768px) {
    right: var(--swiper-navigation-sides-offset, 0px);
  }

  @media (min-width: 1650px) {
    right: var(--swiper-navigation-sides-offset, -60px);
  }

  @media (min-width: 1832px) {
    right: var(--swiper-navigation-sides-offset, -60px);
  }
}

.swiper-button-prev {
  &:hover {
    &::after {
      transform: translateX(-3px);
    }
  }

  left: var(--swiper-navigation-sides-offset, 35%);

  @media (min-width: 768px) {
    left: var(--swiper-navigation-sides-offset, 0px);
  }

  @media (min-width: 1650px) {
    left: var(--swiper-navigation-sides-offset, -60px);
  }

  @media (min-width: 1832px) {
    left: var(--swiper-navigation-sides-offset, -60px);
  }
}

.swiper-button-disabled {
  opacity: 0;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.icon--mirror {
  &::before {
    transform: scaleX(-1);
    display: inline-block;
  }
}



.user-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 136%;

  @media (min-width: 992px) {
    font-size: 20px;
    line-height: 140%;
  }

  & > * {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.block-backplate {
  background-color: #fff;
  border-radius: 20px;

  @media (min-width: 992px) {
    padding: 30px;
  }
}

.lhfw {
  line-height: 140%;
  font-weight: 400;

  p, span, a, li, label, input, textarea, select, button {
    line-height: 140%;
    font-weight: 400;
  }
}